/* main */
html {
  font-size: 18px;
  line-height: 1.77778; }

body {
  font-family: "Inter", "Arial", sans-serif;
  color: #3c4c54; }

.container {
  width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

/* Страница с компонентами */
.component-wrapper {
  padding: 40px 0;
  background-color: #fff; }
  .component-wrapper + .component-wrapper {
    box-shadow: 0 -1px 0 0 #333; }

/* components */
.title {
  font-size: 36px;
  line-height: 1.21028;
  font-weight: 600; }
  .title a {
    color: #3c4c54;
    text-decoration: none;
    transition: color 0.3s ease; }
    @media (min-width: 1080px) {
      .title a:hover, .title a:focus {
        color: #475963; } }
  .title--upper {
    text-transform: uppercase; }
  .title--green {
    color: #009a9d; }
    .title--green a {
      color: #009a9d; }
      @media (min-width: 1080px) {
        .title--green a:hover, .title--green a:focus {
          color: #00b3b7; } }
  .title--size-md {
    font-size: 32px;
    line-height: 1.21031; }
  .title--size-sm {
    font-size: 24px;
    line-height: 1.20833; }
  .title--size-xs {
    font-size: 18px;
    line-height: 1.77778;
    font-weight: 700; }

.arrow-link {
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  line-height: 1.22222;
  color: #009a9d;
  font-weight: 500;
  text-transform: lowercase;
  text-decoration: none;
  transition: color 0.3s ease; }
  .arrow-link--large-arrow .arrow-link__icon {
    width: 90px; }
  .arrow-link__icon {
    position: relative;
    overflow: hidden;
    display: inline-block;
    vertical-align: bottom;
    width: 46px;
    min-width: 46px;
    height: 22px;
    margin-left: 16px;
    transition: transform 0.3s ease; }
    .arrow-link__icon svg {
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      display: block;
      fill: #009a9d;
      transition: fill 0.3s ease; }
  @media (min-width: 1080px) {
    .arrow-link:hover, .arrow-link:focus {
      color: #00b3b7; }
      .arrow-link:hover .arrow-link__icon, .arrow-link:focus .arrow-link__icon {
        transform: translateX(10px); }
        .arrow-link:hover .arrow-link__icon svg, .arrow-link:focus .arrow-link__icon svg {
          fill: #00b3b7; } }

/*blocks*/
